@use 'global' as *;

.p-introduction {
  background-color: $bg-gray;
  position: relative;

}

.p-introduction__title-wrapper {
  position: absolute;
  text-align: center;
  width: 100%;
  top: myrem(-68);
  left: 50%;
  transform: translateX(-50%);

  @include mq('md') {
    top: myrem(-90);

  
}}


.p-introduction__wrapper {
  
  margin-inline: auto;
  max-width: myrem(1200);
}

.p-introduction__box {

  @include mq('md') {
    display: flex;
  }
}

.p-introduction__box + .p-introduction__box {
  margin-top: myrem(30);
}

.p-introduction__img {
  width: 100%;

  @include mq('md') {
    max-width: myrem(466);
  }
  

}

.p-introduction__img img {
  aspect-ratio: 466 / 300;
  object-fit: cover;

}

.p-introduction__body {

  @include mq('md') {
    margin-left: myrem(72);
    flex: 0 0 myrem(662);
  }
  
}

.p-introduction__hall {
  margin-top: myrem(16);
  font-size: myrem(18);
  font-weight: 500;
  letter-spacing: 0.08em;

  @include mq('md') {
    font-size: myrem(24);
  }
}

.p-introduction__text {
  margin-top: myrem(20);
  font-size: myrem(14);
  font-weight: 500;
  line-height: 1.8;

  @include mq('md') {
    margin-top: myrem(32);
    font-size: myrem(16);
  }
}

.p-introduction__meta {
  margin-top: myrem(30);

  @include mq('md') {
    margin-top: myrem(11);
  }

}

.p-introduction__meta-item {
  display: flex;
  align-items: center;
}

.p-introduction__meta-item + .p-introduction__meta-item {
  margin-top: myrem(14);

  @include mq('md') {
    margin-top: myrem(16);
  }
}

.p-introduction__meta-title {
  font-size: myrem(14);
  font-weight: 500;
  line-height: calc(30 / 16);
  background-color: $white;
  min-width: myrem(70);
  text-align: center;

  @include mq('md') {
    font-size: myrem(16);
    line-height: calc(30 / 16);
    min-width: myrem(100);
  }

}

.p-introduction__meta-text {
  margin-left: myrem(10);
  font-size: myrem(14);
  font-weight: 500;
  line-height: calc(24 / 16);

  @include mq('md') {
    margin-left: myrem(16);
    font-size: myrem(16);
    line-height: calc(30 / 16);
  }

}

.p-introduction__btn {
  text-align: right;
  margin-top: myrem(24);

  @include mq('md') {
    margin-top: inherit;
  }

}