@use 'global' as *;

.p-hall-plan {
  position: relative;
}

.p-hall-plan::before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  background: url(../images/hall-plan-bg1.png)no-repeat left center / contain;
  width: myrem(200);
  aspect-ratio: 672 / 522;
  z-index: -2;

  @include mq('md') {
    width: myrem(672);

  }
}

.p-hall-plan::after {
  position: absolute;
  content: '';
  bottom: 0;
  right: 0;
  background: url(../images/hall-plan-bg2.png)no-repeat right bottom / contain;
  aspect-ratio: 500 / 447;
  width: myrem(200);
  z-index: -2;

  @include mq('md') {
    width: myrem(500);
  }
}

.p-hall-plan__title {
  text-align: center;

}

.p-hall-plan__wrapper {
  max-width: myrem(1200);
  margin: myrem(30) auto 0;

}

.p-hall-plan__img {
  width: 100%;

}

.p-hall-plan__img img {
  aspect-ratio: 1200 / 562;
  object-fit: cover;

}