@use 'global' as *;

.p-detail-article {

}

.p-detail-article__wrapper {

  @include mq('md') {
    padding-right: myrem(245);
    padding-left: myrem(245);
  }
}

.p-detail-article__card {

}

.p-detail-article__date {
  font-size: myrem(12);
  font-weight: 500;
  letter-spacing: 0.04em;
  color: $text_gray_day;

  @include mq('md') {
    font-size: myrem(15);
  }
}

.p-detail-article__title {
  margin-top: myrem(16);
  font-size: myrem(18);
  font-weight: 600;
  line-height: calc(32 / 28);

  @include mq('md') {
    margin-top: myrem(33);
    font-size: myrem(28);
  }
}

.p-detail-article__img {
  margin-top: myrem(20);
  width: 100%;
  aspect-ratio: 1080 / 624;
  overflow: hidden;

  @include mq('md') {
    margin-top: myrem(40);
  }
}

.p-detail-article__img img {
  aspect-ratio: 1080 / 624;
  object-fit: cover;
}

.p-detail-article__card p {
  margin-top: myrem(20);
  font-size: myrem(14);
  font-family: $font-sub;
  line-height: 1.8;

  @include mq('md') {
    padding-top: myrem(34);
    padding-bottom: myrem(34);
    font-size: myrem(16);
  }
}

.p-detail-article__card h2 {
  font-size: myrem(18);
  font-weight: 600;
  letter-spacing: 0.04em;
  padding-top: myrem(21);
  padding-bottom: myrem(7);
  border-bottom: 2px solid #A6A5A5;

  @include mq('md') {
    font-size: myrem(24);

  }
}

.p-detail-article__pagination {
  margin-top: myrem(40);
  max-width: myrem(200);
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: myrem(14);
  margin-inline: auto;

  @include mq('md') {
    margin-top: myrem(80);
    gap: myrem(22);
    max-width: myrem(264);
  }
}

.p-detail-article__pagination-prev {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: myrem(12);
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.04em;
  border: 1px solid $line-gray;
  border-radius: myrem(10);
  width: myrem(30);
  height: myrem(30);
  transition: .5s;

  @include mq('md') {
    width: myrem(48);
    height: myrem(48);
    font-size: myrem(17);
  }
}

.p-detail-article__pagination-link {
  font-family: $font-sub;
  font-size: myrem(14);
  font-weight: 400;
  line-height: 2;
  letter-spacing: 0.04em;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid $line-gray;
  border-radius: myrem(10);
  border: 1px solid $line-gray;
  width: myrem(100);
  height: myrem(30);
  white-space: nowrap;

  @include mq('md') {
    font-size: myrem(17);
    width: myrem(120);
    height: myrem(48);
  }
}

.p-detail-article__pagination-next {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: myrem(12);
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.04em;
  border: 1px solid $line-gray;
  border-radius: myrem(10);
  width: myrem(30);
  height: myrem(30);
  transition: .5s;

  @include mq('md') {
    width: myrem(48);
    height: myrem(48);
    font-size: myrem(17);
  }
}

.p-detail-article__pagination-prev:hover {

  @include mq('md') {
    background-color: $key;
    border: 1px solid $key;
    color: $white;
  }
}

.p-detail-article__pagination-next:hover {

  @include mq('md') {
    background-color: $key;
    border: 1px solid $key;
    color: $white;
  }
}

.p-detail-article__pagination-link:hover {
  color: $white;
  background-color: $key;
  border: 1px solid $key;
}