@use 'global' as *;

.p-consultation-pre {
  position: relative;
  overflow: hidden;
}

.p-consultation-pre::before {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: myrem(200);
  height: myrem(125);
  background: url("../images/consultation-bg2.png")no-repeat center center / cover;
  z-index: -1;

  @include mq('md') {
    bottom: myrem(-140);
    width: myrem(700);
    height: myrem(625);
  }

}

.p-consultation-pre__title {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.p-consultation-pre__wrapper {
  margin-top: myrem(30);
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: myrem(24);

  @include mq('md') {
    grid-template-columns: repeat(3, 1fr);
    margin-top: myrem(62);
    padding-left: myrem(185);
    padding-right: myrem(185);
    gap: myrem(36);
  }
}

.p-consultation-pre__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $bg-pale;
  box-shadow: myrem(4) myrem(4) myrem(18) rgba($color:#623F0F, $alpha: .12);
  height: 100%;
}

.p-consultation-pre__box-top {
  display: flex;
  flex-direction: column;
  justify-content:space-between;
  align-items: center;
  background-color: $white;
  width: 100%;
  flex-grow: 0;
  padding-top: myrem(20);
  padding-bottom: myrem(15);

  @include mq('md') {
    padding-top: myrem(40);
    padding-bottom: myrem(31);
  }
}

.p-consultation__card-title {
  text-align: center;
  font-family: $font-sub;
  font-weight: 500;
  font-size: myrem(18);
  line-height: calc(30 / 24);

  @include mq('md') {
    font-size: myrem(24);
  }
}

.p-consultation__card-img {
  position: relative;
  margin-top: myrem(15);
  height: myrem(137);

  @include mq('md') {
    margin-top: myrem(30);
  }
}

.p-consultation__card-img::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: $bg-pale;
  width: myrem(110);
  height: myrem(110);
  border-radius: 50%;
  z-index: 1;
}

.p-consultation__card-img img {
  position: relative;
  z-index: 2;
  top: 50%;
  transform: translateY(-50%);

}

.p-consultation-pre__box-bottom {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  background-color: $bg-pale;
  padding: myrem(12) myrem(16);

  @include mq('md') {
    padding: myrem(24) myrem(32);
  }
}

.p-consultation-pre__card:nth-child(n+4) {
  
  .p-consultation-pre__box-top {
    @include mq('md') {
      height: myrem(297);
    }
  }
}

.p-consultation-pre__card-text {
  font-family: $font-sub;
  font-weight: 500;
  font-size: myrem(14);
  line-height: 1.8;

  @include mq('md') {
    font-size: myrem(16);
  }
}

