@use 'global' as *;

.p-info-company {

}

.p-info-company__lists {
  max-width: myrem(856);
  margin-inline: auto;
}

.p-info-company__list {

}

.p-info-company__items {
  display: flex;
  align-items: center;
}

.p-info-company__item-title {
  font-size: myrem(12);
  font-weight: 600;
  letter-spacing: 0.04em;
  color: $text_gray_day;
  width: myrem(196);
  text-align: left;
  border-bottom: myrem(2) solid #B8B8B8;
  padding: myrem(20) 0 ;

  @include mq('md') {
    font-size: myrem(16);
    padding: myrem(32) 0 ;
  }
}

.p-info-company__item-title--top {
  border-top: myrem(2) solid #B8B8B8;
}

.p-info-company__item-text {
  font-size: myrem(12);
  font-weight: 500;
  line-height: 1.6;
  letter-spacing: 0.02em;
  width: myrem(604);
  margin-left: myrem(28);
  text-align: left;
  border-bottom: myrem(2) solid #DDDDDD;
  padding: myrem(20) 0 ;

  @include mq('md') {
    padding: myrem(32) 0 ;
    font-size: myrem(16);
    margin-left: myrem(56);

  }
}

.p-info-company__item-text--top {
  border-top: myrem(2) solid #DDDDDD;
}

.p-info-company__item-title--address {
  padding: myrem(30) 0;

  @include mq('md') {
    padding: myrem(45) 0;
  }
}

.p-info-company__map {
  margin-top: myrem(60);
  max-width: myrem(1200);
  margin-inline: auto;
  height: auto;

  @include mq('md') {
    margin-top: myrem(120);

  }
}

.p-info-company__map iframe {
  aspect-ratio: 700 / 572;
  object-fit: cover;
  width: 100%;
  height: 100%;

  @include mq('md') {
    aspect-ratio: 1200 / 572;

  }
}
