@use 'sass:math';

// インナー幅
$innerWidth: 1570px;

// 余白
$padding-pc: 25px;
$padding-sp: 20px;

// フォント
$font-main: "Yu Mincho","YuMincho",serif;
$font-en: "EB Garamond", serif;
$font-sub: "游ゴシック体", YuGothic, "游ゴシック", "Yu Gothic", sans-serif;

// 色指定
$black: #16120E;
$white: #fff;
$bg-main: #F0EFEA;
$key: #BF9C6C;
$text-sub: #C6924B;
$line-gray: #D5D5D5;
$line-lightgray: #C7C7C7;
$bg-gray: #DBD8CF;
$bg-pale: #EFEDE7;
$text_gray_day: #6E6E6E;

// ブレイクポイント
$breakpoints: (
  sm: 600,
  md: 768,
  lg: 1024,
  xl: 1440,
);

// メディアクエリ
$mediaquerys: (
  sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
);

// メディアクエリを "@include mq(){}" で書くことができる
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}