@use 'global' as *;

.p-consultation-member {
  position: relative;
  background-color: $bg-pale;
}

.p-consultation-member::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: myrem(320);
  background: url("../images/consultation-member_mv.jpg")no-repeat center center / cover;

  @include mq('md') {
    height: myrem(640);
  }
}

.p-consultation-member__wrapper {
  position: relative;
  z-index: 2;
  margin-inline: auto;

  @include mq('md') {
    padding-left: myrem(185);
    padding-right: myrem(185);  
  }
}

.p-consultation-member__contents {
  background-color: $white;
  padding: myrem(20) myrem(10);

  @include mq('md') {
    padding: myrem(80) myrem(128);
  }
}

.p-consultation-member__contents + .p-consultation-member__contents {
  margin-top: myrem(32);

  @include mq('md') {
    margin-top: myrem(64);
  }
}

.p-consultation-member__title {
  text-align: center;
  position: relative;
  padding-bottom: myrem(20);

  @include mq('md') {
    padding-bottom: myrem(30);

  }
}

.p-consultation-member__title::before {
  position: absolute;
  content: attr(data-en);
  font-family: $font-en;
  font-size: myrem(13);
  font-weight: 400;
  letter-spacing: 0.08em;
  color: $text-sub;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);

  @include mq('md') {
    font-size: myrem(17);

  }
}

.p-consultation-member__title-text {
  display: inline-block;
  font-weight: 600;
  font-size: myrem(20);
  letter-spacing: 0.08em;
  position: relative;
  padding-left: myrem(30);
  padding-right: myrem(30);

  @include mq('md') {
    font-size: myrem(32);
    padding-left: myrem(101);
    padding-right: myrem(101);
  
  }

}

.p-consultation-member__title-text::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: myrem(20);
  height: 1px;
  background-color: $key;

  @include mq('md') {
    width: myrem(80);
  }
}

.p-consultation-member__title-text::after {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  width: myrem(20);
  height: 1px;
  background-color: $key;

  @include mq('md') {
    width: myrem(80);
  }
}


.p-consultation-member__contents-top {
  margin-top: myrem(32);
  font-size: myrem(14);
  font-weight: 600;
  line-height: 1.8;
  text-align: center;

  @include mq('md') {
    margin-top: myrem(64);
    font-size: myrem(16);
  }
}

.p-consultation-member__contents-box {
  max-width: myrem(669);
  margin-inline: auto;
  margin-top: myrem(24);
  padding: myrem(20) myrem(10);
  border: 1px solid $bg-gray;
  counter-reset: number 0;

  @include mq('md') {
    padding: myrem(40);

  }
}

.p-consultation-member__contents-box-text {
  font-size: myrem(14);
  font-weight: 500;
  line-height: 2;
  text-indent: -2em;
  padding-left: 2em;

  @include mq('md') {
    font-size: myrem(18);
  }
}

.p-consultation-member__contents-box-text::before {
  margin-right: 1em;
  counter-increment: number 1;
  content: counter(number)" ";

}

.p-consultation-member__box1 {
  margin-top: myrem(20);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: myrem(30) myrem(10);
  max-width: myrem(669);
  margin-inline: auto;

  @include mq('md') {
    margin-top: myrem(40);
    grid-template-columns: repeat(4, 1fr);
    gap: myrem(27);
  }
}

.p-consultation-member__box1-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items:center;
  justify-content: flex-end;
}

.p-consultation-member__box1-item::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: myrem(147);
  height: myrem(147);
  border-radius: 50%;
  border: 1px solid $key;

}

.p-consultation-member__box1-itemTitle {
  margin-top: myrem(20);
  font-size: myrem(14);
  font-weight: 600;
  line-height: 1.2;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;

  @include mq('md') {
    margin-top: myrem(20);
    font-size: myrem(16);  
  }
}

.p-consultation-member__box1-itemTitle--small {
  font-size: myrem(12);

  @include mq('md') {
    font-size: myrem(14);
  }
}

.p-consultation-member__box1-itemNumber {
  display: flex;
  justify-content: center;
}

.p-consultation-member__box1-itemNumberText {
  display: inline-block;
  font-weight: 500;
  font-size: myrem(56);
  padding-right: myrem(43);
  position: relative;

}

.p-consultation-member__box1-itemNumberText::before {
  position: absolute;
  content: "%";
  font-size: myrem(21);
  top: myrem(13);
  right: myrem(20);
}

.p-consultation-member__box1-itemNumberText::after {
  position: absolute;
  content: "OFF";
  font-size: myrem(21);
  bottom: myrem(17);
  right: myrem(-4);
}


.p-consultation-member__box1-itemNumberBox {
  text-align: left;
}

.p-consultation-member__box1-itemNumberBox1 {
  font-weight: 500;
  font-size: myrem(21);
  line-height: 1.8;
}

.p-consultation-member__box1-itemNumberBox2 {
  font-weight: 500;
  font-size: myrem(21);
  line-height: 1.8;
}

.p-consultation-member__middle-text {
  margin-top: myrem(40);
  font-size: myrem(14);
  font-weight: 500;
  line-height: 1.8;
  text-align: center;

  @include mq('md') {
    font-size: myrem(16);
  }
}

.p-consultation-member__items {
  width: 70%;
  margin-inline: auto;
  margin-top: myrem(30);
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: myrem(10);

  @include mq('md') {
    width: 100%;
    max-width: myrem(701);
    margin-top: myrem(48);
    grid-template-columns: repeat(3, 1fr);
    gap: myrem(16);
  }
}

.p-consultation-member__item {
  background-color: $bg-pale;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding:myrem(10) myrem(7) myrem(10) myrem(8);
}

.p-consultation-member__item-text1 {
  font-size: myrem(14);
  font-weight: 600;
  line-height: 1.5;
  text-align: center;

  @include mq('md') {
    font-size: myrem(16);
  } 
}

.p-consultation-member__item-text2 {
  font-size: myrem(10);
  font-weight: 500;
  line-height: 1.5;
  letter-spacing: -0.08em;

  @include mq('md') {
    font-size: myrem(12);
  }
}

.p-consultation-member__bottom-text {
  margin-top: myrem(30);
  font-size: myrem(14);
  font-weight: 500;
  line-height: 1.8;
  text-align: center;

  @include mq('md') {
    margin-top: myrem(56);
    font-size: myrem(16);
  }
}




