@use 'global' as *;

.p-notice-mv {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.p-notice-mv__bg {
  position: relative;
  width: 100%;
  height: myrem(352);

  @include mq('md') {
    height: myrem(719);
  }
}

.p-notice-mv__bg::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../images/notice-mv.jpg) no-repeat 50% center / cover;
}