@use 'global' as *;

.p-offering-order {
  position: relative;
}

.p-offering-order::before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../images/order-bg.jpg)no-repeat center center / cover;
  z-index: -2;
}

.p-offering-order__title {
  text-align: center;
  font-size: myrem(20);
  font-weight: 500;
  letter-spacing: 0.08em;

  @include mq('md') {
    font-size: myrem(32);
  }
}

.p-offering-order__text {
  font-family: $font-sub;
  max-width: myrem(800);
  margin-inline: auto;
  margin-top: myrem(20);
  font-size: myrem(12);
  font-weight: 500;

  @include mq('md') {
    margin-top: myrem(32);
    font-size: myrem(16);
  }
}

.p-offering-order__comments {
  margin-top: myrem(20);
  text-align: center;
  font-family: $font-sub;
  font-size: myrem(16);
  font-weight: 500;
  line-height: 1.8;

  @include mq('md') {
    margin-top: myrem(40);
    font-size: myrem(20);

  }

}

.p-offering-order__wrapper {
  max-width: myrem(1328);
  margin-top: myrem(24);
  margin-inline: auto;
  background-color: $white;
  padding: myrem(20);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: myrem(20);

  @include mq('md') {
    padding: myrem(64);
    grid-template-columns: repeat(4, 1fr);
    gap: myrem(40);
  }
}

.p-offering-order__img {
  width: 100%;
  background-color: gray;

  @include mq('md') {
    width: myrem(270);
  }
}

.p-offering-order__img img {
  aspect-ratio: 270 / 270;
  object-fit: cover;
}

.p-offering-order__body {
  text-align: center;
}

.p-offering-order__item-box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: myrem(10);

  @include mq('md') {
    margin-top: myrem(16);
  }
}

.p-offering-order__number {
  font-size: myrem(14);

  @include mq('md') {
    font-size: myrem(16);
  }
}

.p-offering-order__item-title {
  font-size: myrem(14);
  font-weight: 600;

  @include mq('md') {
    font-size: myrem(16);
    margin-left: myrem(8);
  }
}

.p-offering-order__item-price {
  font-family: $font-sub;
  font-size: myrem(14);
  font-weight: 500;
  line-height: 2;

  @include mq('md') {
    line-height: 3;
    font-size: myrem(16);
  }
}

