@use 'global' as *;

.p-news {
  overflow: hidden;
  position: relative;
}

.p-news::before {
  display: inline-block;
  position: absolute;
  content: "";
  background: url(../images/news-bg1.png) no-repeat center right/contain;
  bottom: myrem(-270);
  right: myrem(-100);
  width: 75%;
  height: 75%;
  z-index: -1;

  @include mq('md') {
    bottom: myrem(-278);
    right:0;
  }
}

.p-news__wrapper {
  
  position: relative;

  @include mq('md') {
    display: flex;
    justify-content: center;
    padding-bottom: myrem(39);
  }
}

.p-news__bg {
  position: absolute;
  content: "";
  background-color: $bg-pale;
  width: 100%;
  height: 94%;
  z-index: -1;
  top: 0;
  margin-left: calc(-27% - 50vw);

  @include mq('md') {
    height: 86%;
    margin-left: calc(-15.8% - 50vw);
  }
}


.p-news__title {

  @include mq('md') {
    margin-right: myrem(30);
  }
}

.p-news__lists {
  width: 100%;
  margin-top: myrem(20);

  @include mq('md') {
    width: myrem(800);
    margin-top: initial;
    margin-left: myrem(45);

  }
}

.p-news__list {
  display: flex;
  align-items: center;
  padding-right: myrem(10);
  padding-bottom: myrem(10);
  margin-bottom: myrem(12);
  border-bottom: 1px solid $line-lightgray;

  @include mq('md') {
    padding-right: myrem(20);
    padding-bottom: myrem(20);
    margin-bottom: myrem(18);  
  }

}

.p-news__date {
  font-size: myrem(12);
  font-weight: 500;
  color: #6E6E6E;

  @include mq('md') {
    font-size: myrem(14);
  }
}

.p-news__text {
  display: inline-block;
  margin-left: myrem(20);
  font-size: myrem(12);
  font-weight: 500;
  position: relative;

  @include mq('md') {
    margin-left: myrem(40);
    font-size: myrem(14);
  }
}

.p-news__list:hover {
  @include mq('md') {
    opacity: 1;
  }
}

.p-news__text::before {
  position: absolute;
  content: "";
  background-color: $black;
  display: inline-block;
  width: 100%;
  height: 1px;
  bottom: 0;
  transform: translateY(200%);
  transition: .5s;
  opacity: 0;
}

.p-news__list:hover .p-news__text::before {
  @include mq('md') {
    transform: translateY(0%);
    opacity: 1;
    
  }
  
}

.p-news__btn {
  margin-top: myrem(40);
  text-align: center;

  @include mq('md') {
    margin-top: myrem(6);
  }
}

