@use 'global' as *;

.c-drawer-content {
  background-color: $white;
  height: 100vh;
  width: 100%;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  /* clip-path: inset(0% 0% 0% 100%); */

  @include mq('md') {
    display: none;
  }
}

.c-drawer-content__lists {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex-direction: column;
  z-index: 100;
  
}

.c-drawer-content__list {
  overflow: hidden;
}

.c-drawer-content__list + .c-drawer-content__list {
  margin-top: myrem(20);
}

.c-drawer-content__list a {
  display: inline-block;
  color: $black;
  font-weight: 500;
  font-size: myrem(16);
  padding: myrem(10);
  opacity: 0;
  visibility: hidden;
  /* transform: translateY(100%); */
}

.c-drawer-content__list span {
  display: block;
  text-align: center;
  margin-top: myrem(10);
}

.c-drawer-content__btn {
  display: inline-block;
  background-color: $black;
  letter-spacing: 0.04em;
  width: myrem(125);
  color: $white;
  text-transform: uppercase;
  padding:myrem(10) myrem(36);
}

.c-drawer-content__list .c-drawer-content__btn {
  font-size: myrem(16);
}