@use 'global' as *;

.p-price {
  text-align: center;

}

.p-price__title {

}

.p-price__img {
  margin-top: myrem(24);
  display: inline-block;
  max-width: myrem(1200);
  position: relative;

  @include mq('md') {
    margin-top: myrem(34);

  } 
}

.p-price__img::before {
  position: absolute;
  content: "";
  background: url(../images/price-bg.png) no-repeat center left/contain;
  top: myrem(-100);
  left: myrem(-100);
  width: 100%;
  height: 100%;
  z-index: -1;

  @include mq('md') {
    top: myrem(-286);
    left: myrem(-276);
  }

}

.p-price__img img {
  aspect-ratio: 1200 / 606;
  object-fit: cover;
}