@use 'global' as *;

.p-hall-equipment {
  position: relative;
}

.p-hall-equipment::before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../images/hall-equipment_bg.jpg)no-repeat center center / cover;
  z-index: -2;
}

.p-hall-equipment__title-wrapper {
  text-align: center;
  width: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);

  top: myrem(-56);

  @include mq('md') {
    top: myrem(-76);
  }
}

.p-hall-epuipment__wrapper {
  max-width: myrem(1200);
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: myrem(40);

  @include mq('md') {
    grid-template-columns: repeat(2, 1fr);
    gap: myrem(40);  
  }
}

.p-hall-equipment__card {
  display: flex;
  flex-direction: column;
}

.p-hall-equipment__card-img {
  width: 100%;
}

.p-hall-equipment__card-img img {
  aspect-ratio: 580 / 326;
  object-fit: cover;
}

.p-hall-equipment__card-title {
  text-align: center;
  margin-top: myrem(12);
  font-size: myrem(18);
  font-weight: 500;

  @include mq('md') {
    margin-top: myrem(24);
    font-size: myrem(22);
  }

}

.p-hall-equipment__card-text {
  margin-top: myrem(10);
  font-size: myrem(12);
  line-height: 1.9;

  @include mq('md') {
    margin-top: myrem(16);
    font-size: myrem(16);
  }
}