@use 'global' as *;

.p-header {
  background-color: $white;
  width: 100%;
  height: 50px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  transition: .5s;

  @include mq('md') {
    height: 100px;
  }
}

.p-header--top {
  background-color: transparent;

}

.p-header.change-color {
  background-color: $white;
}

.p-header__wrapper {
  width: inherit;
  height: inherit;
  display: flex;
  align-items: center;
}

.p-header__logo {
  position: relative;
  z-index: 1000;
}

.p-header__logo a {
  display: inline-block;
  font-size: myrem(20);
  font-weight: 500;
  letter-spacing: 0.04em;
  color: $black;
  padding: myrem(10);
  transition: .3s;

  @include mq('md') {
    font-size: myrem(30);
  }
}

.p-header__logo--top a {
  color: $white;
  transition: .5s;
}

.p-header__logo--top a.is-active {
  color: $black;
}

.p-header__logo--top.change-color a {
  color: $black;
}

.p-header__logo--top.is-active a {
  color: $black;
} 

.p-header__nav {
  margin-left: auto;
  display: flex;
  height: inherit;
  align-items: center;
}

.p-header__lists {
  display: none;

  @include mq('md') {
    display: flex;
    margin-right: myrem(12);
  }
}

.p-header__lists.change-color .p-header__list {
  color: $black;
  text-shadow: none;
}

.p-header__lists.change-color .p-header__list:hover {
  opacity: 1;
  color: $key;
}

.p-header__lists.change-color .p-header__list-mark {
  color: $black;
  text-shadow: none;
}

.p-header__lists.change-color .p-header__list-mark::before {
  background-color: $key;
}

.p-header__list {
  color: $black;
  font-size: myrem(16);
  font-weight: 600;
  letter-spacing: 0.04em;
  text-shadow: myrem(1) myrem(1) myrem(4) rgba($black, $alpha: 0.20);
  transition: .5s;
  position: relative;
}

.p-header__list--top {
  color: $white;
  transition: .5s;

}

.p-header__list::before {
  position: absolute;
  content: "";
  width: myrem(16);
  height: 1px;
  background-color: $key;
  bottom: myrem(-10);
  left: 50%;
  transform: translateX(-50%) translateY(500%);
  transition: .5s;
  opacity: 0;
}

.p-header__list:hover::before {

  @include mq('md') {
    opacity: 1;
    transform: translateX(-50%) translateY(0%);
  }
}

.p-header__list:hover {

  @include mq('md') {
    opacity: 1;
    color: $key;
  }
}

.p-header__list-nav {
  position: absolute;
  bottom: myrem(-130);
  left: 50%;
  transform: translateX(-50%);
  width: myrem(194);
  height: myrem(122);
  background-color: $bg-pale;
  border: 2px solid $line-lightgray;
  padding: myrem(2);
  transition: 1s;
  opacity: 0;
  visibility: hidden;
}

.p-header__list-navLists {
  border: 1px solid $line-lightgray;
  width: 100%;
  height: 100%;
  text-align: center;
  padding:myrem(19) myrem(18);
}

.p-header__list-navList:first-child {
  border-top: 1px dotted $line-lightgray;
}

.p-header__list-navList {
  border-bottom: 1px dotted $line-lightgray;
}

.p-header__list-navLink {
  display: inline-block;
  color: $black;
  font-weight: 500;
  font-size: myrem(14);
  letter-spacing: 0.04em;
  padding: myrem(8) 0;
  white-space: nowrap;
}

.p-header__list-navLink:hover {

  @include mq('md') {
    opacity: 1;
    color: $key;
  }
}

.p-header__list a {
  padding: myrem(20);
}

.p-header__list-markLink {
  transition: .5s;
  padding: myrem(20);
}

.p-header__list-mark:hover .p-header__list-nav  {
  opacity: 1;
  visibility: visible;
}

.p-header__list-mark:hover .p-header__list-markLink {
  opacity: 1;
  color: $key;
}

.p-header__list-mark:hover::before {

  @include mq('md') {
    background-color: $key;
  }
}

.p-header__list-mark {
  color: $black;
  font-size: myrem(16);
  font-weight: 600;
  letter-spacing: 0.04em;
  text-shadow: myrem(1) myrem(1) myrem(4) rgba($black, $alpha: 0.20);
  transition: .5s;
  position: relative;
}

.p-header__list-mark--top {
  color: $white;
  transition: .5s;
}

.p-header__list-mark::before {
  position: absolute;
  content: "";
  width: myrem(4);
  height: myrem(4);
  background-color: $white;
  top: 0;
  right: myrem(14);
  transform: rotate(45deg);
  transition: .5s;
} 

.p-header__btn {
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;

  @include mq('md') {
    position: initial;
    height: inherit;
    align-items: center;
    width: initial;
  }
}

.p-header__btn-mail {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% - 70%);
  height: inherit;
  background-color: $key;
  transition: .5s;

  @include mq('md') {
    min-width: myrem(150);
    width: initial;
  }
}

.p-header__btn-mail:hover {

  @include mq('md') {
    opacity: 1;
    background-color: $black;
  }
}

.p-header__btn-mailWrapper {
  display: flex;
  flex-direction: column;
}

.p-header__btn-mailIcon {
  text-align: center;
}

.p-header__btn-mailIcon img {
  display: inline-block;

}

.p-header__btn-mailText {
  display: inline-block;
  color: $white;
  font-size: myrem(12);
  font-weight: 600;
  letter-spacing: 0.04em;

  @include mq('md') {
    font-size: myrem(16);
  }
}

.p-header__btn-tel {
  display: flex;
  width: calc(100% - 30%);
  height: inherit;
  background-color: $black;
  padding-top: myrem(4);
  padding-bottom: myrem(5);
  justify-content: center;
  align-items: center;
  pointer-events: auto;
    
  @include mq('md') {
    pointer-events: none;
    width: initial;
    min-width: myrem(300);
    padding-top: myrem(9);
    padding-bottom: myrem(10);
  }  
}


.p-header__btn-telWrapper {
  color: $white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.p-header__btn-telText-up {
  display: inline-block;
  font-size: myrem(12);
  font-weight: 400;
  letter-spacing: 0.04em;
  border-bottom: 1px solid $white;

  @include mq('md') {
    font-size: myrem(16);
  } 
}

.p-header__btn-telText {
  display: inline-block;
  font-size: myrem(20);
  font-weight: 400;
  letter-spacing: 0.04em;
  line-height: 1;
  position: relative;
  margin-top: myrem(3);
  padding-left: myrem(24);

  @include mq('md') {
    font-size: myrem(30);
    padding-left: myrem(36);
    margin-top: myrem(7);
  }
}

.p-header__btn-telText::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: myrem(18);
  height: myrem(16);
  background: url(../images/tel-icon.svg) no-repeat center right/contain;

  @include mq('md') {
    width: myrem(26);
    height: myrem(23);
  }
}

.p-header__btn-telText-bottom {
  display: inline-block;
  font-size: myrem(12);
  font-weight: 400;
  letter-spacing: 0.04em;

  @include mq('md') {
    font-size: myrem(16);
  }
}