@use 'global' as *;

.p-merit-message {
  background-color: $white;

}

.p-merit-message__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.p-merit-message__lead {
  font-size: myrem(18);
  font-weight: 500;
  line-height: 1.8;
  text-align: center;

  @include mq('md') {
    font-size: myrem(32);

  }
}

.p-merit-message__text {
  margin-top: myrem(30);
  font-family: $font-sub;
  font-size: myrem(14);
  font-weight: 500;
  line-height: 2;
  text-align: center;
  max-width: myrem(912);

  @include mq('md') {
    margin-top: myrem(56);
    font-size: myrem(22);
  }
}

.p-merit-message__img {
  margin-top: myrem(80);
  width: 100%;
  height: auto;

  @include mq('md') {
    margin-top: myrem(160);
  }
}

.p-merit-message__img img {
  width: 100%;
  height: 100%;
  aspect-ratio: 1920 / 700;
  object-fit: cover;
}