@use 'global' as *;

.p-merit-feature {
  position: relative;
}

.p-merit-feature::before {
  position: absolute;
  content: "";
  top: myrem(164);
  left: myrem(-118);
  background: url(../images/merit-feature_bg1.png) no-repeat center center/ contain;
  aspect-ratio: 577 / 515;
  width: myrem(300);
  z-index: -1;

  @include mq('md') {
    width: myrem(750);
    top: myrem(20);
    left: 0;
  }
}

.p-merit-feature::after {
  position: absolute;
  content: "";
  bottom: 0;
  right: 0;
  background: url(../images/merit-feature_bg-bottom.png) no-repeat center center/ contain;
  z-index: -1;
  aspect-ratio: 1183 / 758;
  width: myrem(300);

  @include mq('md') {
    width: myrem(1000);

  }
}

.p-merit-feature__title {
  text-align: center;

}

.p-merit-feature__wrapper {
  max-width: myrem(1200);
  margin-top: myrem(40);
  counter-reset: number 0;

  @include mq('md') {
    margin: myrem(80) auto 0;

  }
}

.p-merit-feature__card {
  display: flex;
  flex-direction: column;

  @include mq('md') {
    flex-direction: row;
  }
}

.p-merit-feature__card + .p-merit-feature__card {
  margin-top: myrem(36);

  @include mq('md') {
    margin-top: myrem(24);
  }
  
}

.p-merit-feature__card:nth-child(even) {

  @include mq('md') {
    flex-direction: row-reverse;

    .p-merit-feature__body {
      margin-left: initial;
      margin-right: myrem(56);
    }
  }
}

.p-merit-feature__img {
  order: 3;
  margin-top: myrem(10);

  @include mq('md') {
    order: initial;
    margin-top: initial;
      flex: 0 0 myrem(544);
  }

}

.p-merit-feature__img img {
  aspect-ratio: 544 / 400;
  object-fit: cover;
}

.p-merit-feature__body {
  margin-top: myrem(12);
  display: contents;

  @include mq('md') {
    display: block;
    margin-top: myrem(24);
    flex: 0 0 myrem(600);
    margin-left: myrem(56);
  }
}

.p-merit-feature__number {
  order: 1;

  @include mq('md') {
    order: initial;
  }
}

.p-merit-feature__number::before {
  counter-increment: number 1;
  content: counter(number,decimal-leading-zero)".";
  color: $text-sub;
  font-family: $font-en;
  font-size: myrem(17);
  letter-spacing: 0.04em; 
}

.p-merit-feature__card-title {
  margin-top: myrem(2);
  font-weight: 600;
  color: $text-sub;
  font-size:myrem(20) ;
  letter-spacing: 0.08em;
  order: 2;

  @include mq('md') {
    margin-top: myrem(16);
    font-size:myrem(28) ;
    order: initial;
  }

}

.p-merit-feature__lead {
  margin-top: myrem(20);
  font-size: myrem(14);
  font-weight: 500;
  letter-spacing: 0.04em;
  order: 4;

  @include mq('md') {
    margin-top: myrem(33);
    font-size: myrem(20);
    order: initial;
  }

}

.p-merit-feature__text {
  font-family: $font-sub;
  margin-top: myrem(14);
  font-size: myrem(12);
  font-weight: 500;
  line-height: 1.8;
  order: 5;

  @include mq('md') {
    font-size: myrem(16);
    margin-top: myrem(24);
    order: initial;
  }

}

