@use 'global' as *;


.c-mv__title-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.c-mv__title-wrapper::before {
  position: absolute;
  content: '';
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: myrem(1);
  height: myrem(48);
  background-color: $black;

  @include mq('md') {
    height: myrem(88);

  }
}

.c-mv__title-wrapper::after {
  position: absolute;
  content: '';
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: myrem(1);
  height: myrem(48);
  background-color: $black;

  @include mq('md') {
    height: myrem(88);
  }
}

.c-mv__title-wrapper--white::before,
.c-mv__title-wrapper--white::after {
  background-color: $white;
}

.c-mv__title {
  font-size: myrem(18);
  font-weight: 500;
  writing-mode: vertical-rl;
  color: $black;
  letter-spacing: 0.2em;

  @include mq('md') {
    font-size: myrem(30);
  }
}

.c-mv__title--white {
  color: $white;
}

.c-mv__title-hall {
  font-size: myrem(14);
  font-weight: 600;

  @include mq('md') {
    font-size: myrem(20);

  }
}