@use 'global' as *;

.p-fv {
  position: relative;
  width: 100%;
  height: 100%;
  aspect-ratio: 800 / 980;
  overflow: hidden;

  @include mq('md') {
    aspect-ratio: 1920 / 980;
  }

}

.p-fv__img {
  position: absolute;
  width: inherit;
  height: inherit;
  overflow: hidden;

}

.p-fv::before {
  position: absolute;
  content: "";
  background-color: rgba($black, $alpha: 0.24);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

.p-fv__img img {
  aspect-ratio: 800 / 980;
  object-fit: cover;
  width: 100%;

  @include mq('md') {
    aspect-ratio: 1920 / 980;

  }
}

.p-fv__wrapper {
  width: inherit;
  height: inherit;
  display: flex;
  align-items: center;
  padding-left: myrem(10);
  padding-right: myrem(10);

  @include mq('md') {
    margin-left: myrem(174);

  }
}

.p-fv__box {
  margin-top: myrem(120);
  position: relative;
  z-index: 2;

  @include mq('md') {
    margin-top: myrem(278);
    margin-right: myrem(769);
  
  }

}

.p-fv__lead {
  font-size: myrem(28);
  letter-spacing: 0.04em;
  font-weight: 500;
  color: $white;
  text-shadow: myrem(2) myrem(2) myrem(6) rgba($black, $alpha: 0.24);

  @include mq('md') {
    font-size: myrem(60);
    letter-spacing: 0.12em;
  }
}

.p-fv__text {
  margin-top: myrem(8);
  font-size: myrem(14);
  letter-spacing: 0.04em;
  font-weight: 500;
  color: $white;
  text-shadow: myrem(1) myrem(1) myrem(3) rgba($black, $alpha: 0.20);

  @include mq('md') {
    font-size: myrem(32);
    margin-top: myrem(26);
  }
}

#slider {
  width: 100%;
  height: 100%;
}


