@use 'global' as *;

.p-notice-archive {

}

.p-notice-archive__wrapper {

  @include mq('md') {
    padding-left: myrem(245);
    padding-right: myrem(245);
  }
}

.p-notice-archive__list {
  border-bottom: 1px solid $line-gray;

}

.p-notice-archive__link {
  width: 100%;
  height: auto;
  display: inline-block;
  padding: myrem(10) myrem(10) myrem(10) 0;

  @include mq('md') {
    padding: myrem(24) myrem(64);
  }
}

.p-notice-archive__date {
  font-size: myrem(12);
  font-weight: 500;
  letter-spacing: 0.04em;
  color: $text_gray_day;

  @include mq('md') {
    font-size: myrem(15);
  }
}

.p-notice-archive__title {
  margin-top: myrem(6);
  font-size: myrem(13);
  font-weight: 500;
  line-height: 1.8;
  position: relative;
  padding-right: myrem(30);

  @include mq('md') {
    padding-right: myrem(263);
    font-size: myrem(17);
    margin-top: myrem(12);
  }
}

.p-notice-archive__title::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: myrem(18);
  height: 1px;
  transform: translateY(-50%);
  background-color: $text_gray_day;
  transition: .5s;
}

.p-notice-archive__title::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: myrem(6);
  height: myrem(6);
  transform: translateY(-50%) rotate(45deg);
  border-top: 1px solid $text_gray_day;
  border-right: 1px solid $text_gray_day;
  transition: .5s;
}

.p-notice-archive__title-line {
  position: relative;
}

.p-notice-archive__title-line::before {
  position: absolute;
  content: "";
  background-color: $black;
  display: inline-block;
  width: 100%;
  height: 1px;
  bottom: 0;
  transform: translateY(200%);
  transition: .5s;
  opacity: 0;
}

.p-notice-archive__list:hover .p-notice-archive__title-line::before {

  @include mq('md') {
    transform: translateY(0%);
    opacity: 1;
  }
}

.p-notice-archive__list:hover .p-notice-archive__title::before {
  
  @include mq('md') {
    right: myrem(-10);
  }
}

.p-notice-archive__list:hover .p-notice-archive__title::after {

  @include mq('md') {
    right: myrem(-10);
  }
}

.p-notice-archive__pagination-inner {
  margin-top: myrem(40);

  @include mq('md') {
    margin-top: myrem(80);
  }
}

.p-notice-archive__pagination {
  max-width: myrem(280);
  display: flex;
  justify-content: space-between;
  margin-inline: auto;

  @include mq('md') {
    max-width: myrem(468);
    gap: myrem(12);
  } 
}

.p-notice-archive__paginationLink {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: myrem(12);
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.04em;
  border: 1px solid $line-gray;
  border-radius: myrem(10);
  width: myrem(30);
  height: myrem(30);
  transition: .5s;

  @include mq('md') {
    width: myrem(48);
    height: myrem(48);
    font-size: myrem(17);
  }
}

.p-notice-archive__paginationReader {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: myrem(12);
  font-weight: 400;
  line-height: 1.5;
  letter-spacing: 0.04em;
  width: myrem(30);
  height: myrem(30);

  @include mq('md') {
    width: myrem(48);
    height: myrem(48);
    font-size: myrem(17);
  }
}

.p-notice-archive__paginationLink:hover {

  @include mq('md') {
    color: $white;
    background-color: $key;
    border: 1px solid $key;
  }
}
