@use 'global' as *;


.p-hall-message__wrapper {
  display: flex;
  flex-direction: column;
  max-width: myrem(1200);
  margin: 0 auto;

  
}

.p-hall-message__img {
  width: 100%;
  margin: 0 auto;
}

.p-hall-message__img img {
  aspect-ratio: 1200 / 611;
  object-fit: cover;
}

.p-hall-message__lead {
  text-align: center;
  margin-top: myrem(30);
  font-size: myrem(20);
  font-weight: 500;
  letter-spacing: 0.08em;

  @include mq('md') {
    margin-top: myrem(60);
    font-size: myrem(32);
  }
}

.p-hall-message__text {
  margin-top: myrem(14);
  font-family: $font-sub;
  font-size: myrem(12);
  font-weight: 500;
  line-height: 1.7;

  @include mq('md') {
    margin-top: myrem(28);
    font-size: myrem(16);
  }

}