@use 'global' as *;

.p-consultation-merit {
  background-color: $bg-gray;

}

.p-consultation-merit__title {
  text-align: center;

}

.p-consultation-merit__wrapper {
  margin-top: myrem(35);
  display: grid;
  grid-template-columns: repeat(1,1fr);
  gap: myrem(32);

  @include mq('md') {
    grid-template-columns: repeat(3,1fr);
    margin-top: myrem(70);
    padding-left: myrem(185);
    padding-right: myrem(185);
  }
}

.p-consultation-merit__card {
  padding: myrem(6);
  background-color: $white;

  @include mq('md') {
    padding: myrem(8);
  }
}


.p-consultation-merit__body {
  border: 1px solid $line-gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  padding: myrem(15);
  height: 100%;

  @include mq('md') {
    padding: myrem(25);
  }

}

.p-consultation-merit__lead {
  font-family: $font-sub;
  font-size: myrem(14);
  font-weight: 700;
  line-height: calc(30 / 16);
  position: relative;
  padding-left: myrem(14);

  @include mq('md') {
    font-size: myrem(16);
    padding-left: myrem(18);
  }
}

.p-consultation-merit__lead::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  left: 0;
  width: myrem(6);
  height: myrem(6);
  background-color: $key;

  @include mq('md') {
    width: myrem(8.5);
    height: myrem(8.5);
  }

}

.p-consultation-merit__text {
  font-family: $font-sub;
  font-size: myrem(12);
  font-weight: 500;
  line-height: 1.8;
  margin-top: myrem(12);

  @include mq('md') {
    font-size: myrem(16);
    margin-top: myrem(16);
  }
}