@use 'global' as *;

.p-consultation-contact {

}

.p-consultation-contact__title {
  text-align: center;

}

.p-consultation-contact__form {
  max-width: myrem(670);
  margin-inline: auto;
  padding-top: myrem(21);

  @include mq('md') {
    padding-top: myrem(42);
  }

}

.p-consultation-contact__item {
  margin-top: myrem(32);
  display: flex;
  flex-direction: column;

  @include mq('md') {
    flex-direction: row;
    align-items: center;
  }
}

.p-consultation-contact__head {
  display: flex;
  align-items: center;
  font-size: myrem(14);
  font-weight: 500;
  letter-spacing: 0.04em;

  @include mq('md') {
    flex: 0 0 myrem(190);
    font-size: myrem(16);
  }
}

.p-consultation-contact__item--top {

  @include mq('md') {
    margin-top: myrem(10);
  }
}

.p-consultation-contact__checkbox-wrapper {
  display: flex;
  max-width: myrem(448);
  flex-wrap: wrap;
  margin-top: myrem(10);
  align-items: center;

  @include mq('md') {
    margin-top: initial;
    margin-left: myrem(32);
  }
}

.p-consultation-contact__checkbox {
  margin-right: myrem(16);
  display: flex;
  align-items: center;

  @include mq('md') {
    margin-right: myrem(32);
  }
}

.p-consultation-contact__radio-wrapper {
  display: flex;
  align-items: center;
  flex-wrap: wrap;

  @include mq('md') {
    margin-left: myrem(32);
  }
}

.p-consultation-contact__radio {
  margin-right: myrem(24);
  line-height: 1.8;
}

input[type="radio"] {
  position: relative;
  width: 16px;
  height: 16px;
  border: 1px solid $text_gray_day;
  border-radius: 50%;
  vertical-align: -2px;
  appearance: none;
}

input[type="radio"]:checked:before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 9px;
  height: 9px;
  border-radius: 50%;
  background: $text_gray_day;
  content: '';
}

.p-consultation-contact__btn {
  text-align: center;
  position: relative;
  margin-top: myrem(48);
}

.p-consultation-contact__btn-arrow {
  display: inline-block;
  position: relative;
}

.p-consultation-contact__btn-arrow::before {
  position: absolute;
  content: "";
  background-color: $white;
  width: myrem(14);
  height: 1px;
  top: 50%;
  transform: translateY(-50%);
  right: myrem(32);
  transition: .5s;
  z-index: 5;

  @include mq('md') {
    width: myrem(16);
  }
}

.p-consultation-contact__btn-arrow::after {
  position: absolute;
  content: "";
  border-top: 1px solid $white;
  border-right: 1px solid $white;
  width: myrem(5);
  height: myrem(5);
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  right: myrem(32);
  transition: .5s;

  @include mq('md') {
    width: myrem(6);
    height: myrem(6);
  }
} 

.p-consultation-contact__btn-arrow:hover::before {

  @include mq('md') {
    right: myrem(20);
  }
}

.p-consultation-contact__btn-arrow:hover::after {

  @include mq('md') {
    right: myrem(20);
  }
}

.p-consultation-contact__submit {
  min-width: myrem(180);
  display: inline-block;
  background-color: $key;
  text-align: center;
  transition: .5s;
  color: $white;
  font-size: myrem(14);
  font-weight: 600;
  letter-spacing: 0.04em;
  padding: myrem(20) myrem(5);
  border: none;

  @include mq('md') {
    min-width: myrem(234);
    font-size: myrem(16);
  }
}

.p-consultation-contact__submit:hover {

  @include mq('md') {
    cursor: pointer;
    background-color: $black;
  }
}

.p-consultation-contact__item-post-code input[type="number"] {

  @include mq('md') {
    flex: 0 0 myrem(128);
  }
}