@use 'global' as *;

.l-offering-order {
  padding-top: myrem(80);
  padding-bottom: myrem(80);
  position: relative;

  @include mq('md') {
    padding-top: myrem(160);
    padding-bottom: myrem(160);
  }
}

.l-offering-order::before {
  position: absolute;
  content: '';
  top: 0;
  right: 0;
  width: 56%;
  height: myrem(549);
  background-color: $bg-pale;
  z-index: -1;
}

.l-offering-order::after {
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  width: 74%;
  height: myrem(868);
  background-color: $bg-pale;
  z-index: -1;
}