@use 'global' as *;

.c-btn1 {
  display: inline-block;
  min-width: myrem(200);
  background-color: $key;
  padding: myrem(16) myrem(8);
  text-align: center;
  transition: .5s;

  @include mq('md') {
    min-width: myrem(351);
    padding: myrem(20) myrem(10);
  }
}

.c-btn1:hover {

  @include mq('md') {
    opacity: 1;
    background-color: $black;
  }
  
}

.c-btn1:hover .c-btn1__text::before {
  right: myrem(-12);
}

.c-btn1:hover .c-btn1__text::after {
  right: myrem(-12);
}

.c-btn1__text {
  color: $white;
  font-size: myrem(14);
  font-weight: 600;
  letter-spacing: 0.04em;
  display: inline-block;
  position: relative;
  padding-left: myrem(28);
  padding-right: myrem(34);
  
  @include mq('md') {
    font-size: myrem(16);
    padding-left: myrem(48);
    padding-right: myrem(56);
  }
}

.c-btn1__text::before {
  position: absolute;
  content: "";
  background-color: $white;
  width: myrem(14);
  height: 1px;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  transition: .5s;

  @include mq('md') {
    width: myrem(16);
  }
}

.c-btn1__text::after {
  position: absolute;
  content: "";
  border-top: 1px solid $white;
  border-right: 1px solid $white;
  width: myrem(5);
  height: myrem(5);
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  right: 0;
  transition: .5s;

  @include mq('md') {
    width: myrem(6);
    height: myrem(6);
  }
}

.c-btn1--min1 {
  min-width: myrem(180);

  @include mq('md') {
    min-width: myrem(234);

  }
}

.c-btn1--min2 {
  min-width: myrem(177);
  padding: myrem(12) myrem(10);
}

.c-btn1__text--min {
  font-size: myrem(14);
  font-weight: 900;    
  padding-left: myrem(25);
  padding-right: myrem(40);
}
