@use 'global' as *;

.p-contact {
  position: relative;
}

.p-contact::after {
  position: absolute;
  content: "";
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image:url(../images/btn-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 25% center;

  @include mq('md') {
    background-position: center center;
  }
}

.p-contact--top {
  position: relative;
}


.p-contact--top::after {
  position: absolute;
  content: "";
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image:url(../images/contact-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 25% center;

  @include mq('md') {
    background-position: center center;
  }
}


.p-contact__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include mq('md') {
    flex-direction: row;
    justify-content: center;
    padding: 0 myrem(185);
  }
}

.p-contact__img {
  position: relative;
  overflow: hidden;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 600 / 292;
  z-index: 1;

  //アニメーション下準備
  /* opacity: 0;
  visibility: hidden;
  transform: translateY(10%); */
}

.p-contact__img + .p-contact__img {
  margin-top: myrem(40);

  @include mq('md') {
    margin-top: initial;
  }
}

.p-contact__img img {
  position: absolute;
  top: 0;
  left: 0;
  aspect-ratio: 600 / 292;
  object-fit: cover;
  transition: .5s;
}

.p-contact__img::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba($black, $alpha: 0.1);
  z-index: 3;
  transition: .5s;
}


.p-contact__title {
  font-weight: 600;
  font-size: myrem(18);
  letter-spacing: 0.08em;
  color: $white;
  position: relative;
  z-index: 4;
  display: block;
  width: 100%;
  text-align: center;

  @include mq('md') {
    font-size: myrem(24);
  }
}

.p-contact__title span {
  display: inline-block;
  border-bottom: 1px solid $white;
}

.p-contact__title::before {
  position: absolute;
  content: "";
  width: myrem(20);
  height: myrem(20);
  border: 1px solid $white;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  right: myrem(25);

  @include mq('md') {
    width: myrem(26);
    height: myrem(26);
    right: myrem(70);
  }
}

.p-contact__title::after {
  position: absolute;
  content: "→";
  top: 52%;
  transform: translateY(-50%);
  right: myrem(25);

  @include mq('md') {
    right: myrem(69);
    top: 50%;
  }
}

.p-contact__img:hover {
  @include mq('md') {
    opacity: 1;
  }
}

.p-contact__img:hover.p-contact__img img {
  @include mq('md') {
    transform: scale(1.1);
  }  
}

.p-contact__img:hover::before {
  @include mq('md') {
    background-color: transparent;
  }
}

.p-contact__wrapper-price {
  margin-top: myrem(40);
  display: flex;
  flex-direction: row;
  justify-content: center;

  @include mq('md') {
    margin-top: initial;
    padding: 0 myrem(185);

  }
}

.p-contact__price-btn {
  position: relative;
  overflow: hidden;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 600 / 292;
  z-index: 1;

  @include mq('md') {
    padding-top: myrem(128);
    padding-bottom: myrem(128);
    aspect-ratio: 1200 / 292;

  }
}

.p-contact__price-btn::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba($black, $alpha: 0.1);
  z-index: 3;
  transition: .5s;
}

.p-contact__price-btn:hover.p-contact__price-btn img {
  @include mq('md') {
    transform: scale(1.1);
  }  
}

.p-contact__price-btn:hover::before {
  @include mq('md') {
    background-color: transparent;
  }
}


.p-contact__price-btn img {
  position: absolute;
  top: 0;
  left: 0;
  aspect-ratio: 600 / 292;
  object-fit: cover;
  transition: .5s;

  @include mq('md') {
    aspect-ratio: 1200 / 292;
  }
}


