@use 'global' as *;

.c-drawer-icon {
  margin-right: myrem(13);
  padding: myrem(12) myrem(10);
  position: relative;
  z-index: 1000;

  @include mq('md') {
    display: none;
  }
}

.c-drawer-icon__bars {
  width: myrem(24);
  height: myrem(16);
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.c-drawer-icon__bar {
  width: myrem(24);
  height: myrem(2);
  background-color: $white;
  border-radius: myrem(20);
  transition: .5s;
}

.c-drawer-icon__bar--color {
  background-color: $black;

}

.c-drawer-icon__bar.change-color {
  background-color: $black;
}

.c-drawer-icon__bar.is-active:first-child {
  
  transform: translateY(7px) rotate(45deg);
  background-color: $black;
}

.c-drawer-icon__bar.is-active:nth-child(2) {
  opacity: 0;
}

.c-drawer-icon__bar.is-active:nth-child(3) {
  transform: translateY(-7px) rotate(-45deg);
  background-color: $black;
}