@use 'global' as *;

.p-footer {
  background-color: $black;
}

.p-footer__wrapper {

  @include mq('md') {
    display: flex;
    justify-content: center;
  }
  

}

.p-footer__logo {
  color: $white;
  font-size: myrem(20);
  font-weight: 500;

  @include mq('md') {
    font-size: myrem(30);
  }
}

.p-footer__address {
  margin-top: myrem(24);
  color: $white;
  font-family: $font-sub;
  font-size: myrem(14);
  font-weight: 500;
  line-height: calc(26 / 16);

  @include mq('md') {
    margin-top: myrem(36);
    font-size: myrem(16);
    line-height: calc(30 / 16);
  }
}

.p-footer__tel {
  color: $white;
  font-family: $font-sub;
  font-size: myrem(16);
  font-weight: bold;
  letter-spacing: 0.04em;
  margin-top: myrem(6);

  @include mq('md') {
    font-size: myrem(20);
    margin-top: myrem(8);
  }
}

.p-footer__tel-link {
  pointer-events: auto;

  @include mq('md') {
    pointer-events: none;
  }
}

.p-footer__tel span {
  display: inline-block;
  font-size: myrem(12);
  font-weight: 500;
  letter-spacing: 0.04em;
  margin-left: myrem(10);
  vertical-align:middle;

  @include mq('md') {
    font-size: myrem(14);
    margin-left: myrem(12);
  }
}

.p-footer__btn {
  margin-top: myrem(30);
  display: inline-block;
  min-width: myrem(200);
  background-color: transparent;
  padding: myrem(15) myrem(8);
  text-align: center;
  transition: .5s;
  border: 1px solid $white;

  @include mq('md') {
    margin-top: myrem(47);
    min-width: myrem(316);
    padding: myrem(20) myrem(10);

  }
}

.p-footer__btn:hover {

  @include mq('md') {
    opacity: 1;
    background-color: $key;
  }
  
}

.p-footer__btn:hover .p-footer__btn-text::before {
  
  @include mq('md') {
    right: myrem(-12);
  }
}

.p-footer__btn:hover .p-footer__btn-text::after {
  
  @include mq('md') {
    right: myrem(-12);
  }
}

.p-footer__btn-text {
  color: $white;
  font-size: myrem(14);
  font-weight: 600;
  letter-spacing: 0.04em;
  display: inline-block;
  position: relative;
  padding-left: myrem(10);
  padding-right: myrem(30);

  @include mq('md') {
    font-size: myrem(16);
    padding-left: myrem(48);
    padding-right: myrem(56);
  }
}

.p-footer__btn-text::before {
  position: absolute;
  content: "";
  background-color: $white;
  width: myrem(14);
  height: 1px;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  transition: .5s;

  @include mq('md') {
    width: myrem(16);
  }
}

.p-footer__btn-text::after {
  position: absolute;
  content: "";
  border-top: 1px solid $white;
  border-right: 1px solid $white;
  width: myrem(5);
  height: myrem(5);
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  right: 0;
  transition: .5s;

  @include mq('md') {
    width: myrem(6);
    height: myrem(6);
  }
}

.p-footer__right {

  @include mq('md') {
    display: flex;
    margin-left: auto;
  }

}

.p-footer__right-menu1 {
  margin-top: myrem(60);

  @include mq('md') {
    margin-top: initial;
  }
}

.p-footer__menu-item {
  font-size: myrem(15);
  font-weight: 500;
  letter-spacing: 0.04em;
  color: $white;
  transition: .5s;

  @include mq('md') {
    font-size: myrem(18);
  }
}

.p-footer__menu-item a {
  display: inline-block;
  position: relative;
}

.p-footer__menu-item a:hover {
  @include mq('md') {
    opacity: 1;
  }
}

.p-footer__menu-item a::before {
  
  @include mq('md') {
    position: absolute;
    content: "";
    background-color: $white;
    width: 100%;
    height: 1px;
    bottom: 0;
    transform: translateY(200%);
    opacity: 0;
    transition: .5s;
  }
}

.p-footer__menu-item a:hover::before {
  
  @include mq('md') {
    transform: translateY(0%);
    opacity: 1;
  }
  
}


.p-footer__menu-item:hover {
  @include mq('md') {
    opacity: 1;
  }
}

.p-footer__menu-item2 {
  font-size: myrem(15);
  font-weight: 500;
  letter-spacing: 0.04em;
  color: $white;

  @include mq('md') {
    font-size: myrem(18);

  }
}

.p-footer__menu-item--lh {
  line-height: calc(30 / 15);

  @include mq('md') {
    line-height: calc(40 / 18);
  }
}

.p-footer__menu-item+.p-footer__menu-item {
  margin-top: myrem(14);
}

.p-footer__menu-item2+.p-footer__menu-item2 {
  margin-top: myrem(18);

  @include mq('md') {
    margin-top: myrem(24);
  }
}

.p-footer__right-menu2 {
  margin-top: myrem(18);

  @include mq('md') {
    margin-top: inherit;
    margin-left: myrem(128);
  }
}

.p-footer__menu-item2 a {
  display: inline-block;
  position: relative;
}

.p-footer__menu-item2 a:hover {
  opacity: 1;
}

.p-footer__menu-item2 a::before {
  position: absolute;
  content: "";
  background-color: $white;
  width: 100%;
  height: 1px;
  bottom: 0;
  transform: translateY(200%);
  opacity: 0;
  transition: .5s;
}

.p-footer__menu-item2 a:hover::before {
  opacity: 1;
  transform: translateY(0%);
}


.p-footer__menu-item2:hover {
  opacity: 1;
}
