@use 'global' as *;

.c-section-title1 {
  font-weight: 500;
  font-size: myrem(24);
  letter-spacing: 0.08em;
  position: relative;

  @include mq('md') {
    font-size: myrem(32);
  }
}

.c-section-title1::before {
  position: absolute;
  content: attr(data-en);
  color: $text-sub;
  font-family: $font-en;
  font-weight: 400;
  font-size: myrem(14);
  letter-spacing: 0.08em;
  bottom: myrem(-16);
  left: 0;

  @include mq('md') {
    font-size: myrem(17);
    bottom: myrem(-25);
  }
}

.c-section-title1--center {
  display: inline-block;
}

.c-section-title1--center::before {
  left: 50%;
  transform: translateX(-50%);
}

