@use 'global' as *;

.p-hall-access {
  position: relative;
}

.p-hall-access::before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  background: url(../images/hall-equipment_bg.jpg)no-repeat center center / cover;
  width: 100%;
  height: 100%;
  z-index: -2;
}

.p-hall-access__title-wrapper {
  text-align: center;
  width: 100%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: myrem(-46);

  @include mq('md') {
    top: myrem(-54);
  }
}


.p-hall-access__wrapper {
  max-width: myrem(1200);
  margin: 0 auto;

}

.p-hall-access__map {
  width: 100%;
  height: auto;

}

.p-hall-access__map iframe {
  aspect-ratio: 700 / 572;
  object-fit: cover;
  width: 100%;
  height: 100%;

  @include mq('md') {
    aspect-ratio: 1200 / 572;
  }
}

.p-hall-access__meta {
  margin-top: myrem(26);

  @include mq('md') {
    margin-top: myrem(47);
  }

}

.p-hall-access__meta dl {
  display: flex;
  flex-direction: column;

  @include mq('md') {
    flex-direction: row;
  }
}

.p-hall-access__meta dl + dl {
  margin-top: myrem(16) ;
}

.p-hall-access__meta dl dt {
  font-size: myrem(14);
  font-weight: 600;
  width: myrem(64);

  @include mq('md') {
    font-size: myrem(16);
  }

}

.p-hall-access__meta dl dd {
  font-size: myrem(14);
  font-weight: 500;
  margin-top: myrem(6);

  @include mq('md') {
    font-size: myrem(16);
    margin-top: initial;
    margin-left: myrem(80);

  }
}