@use 'global' as *;

.p-offering-mv {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.p-offering-mv__bg {
  position: relative;
  width: 100%;
  height: myrem(352);

  @include mq('md') {
    height: myrem(719);
  }
}

.p-offering-mv__bg::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(../images/offering-mv.jpg) no-repeat 27% center / cover;
  z-index: -2;
}

.p-offering-mv__bg::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($black, $alpha: .24);
  z-index: -1;
}


