@use 'global' as *;

.p-offering-form {
  background-color: $bg-gray;

}

.p-offering-form__wrapper {
  max-width: myrem(923);
  margin-inline: auto;
  background-color: $white;
  padding: myrem(40) myrem(10) myrem(40) myrem(10);

  @include mq('md') {
    padding: myrem(56) myrem(130) myrem(80) myrem(123);

  }

}

.p-offering-form__title {
  text-align: center;

}

.p-offering-form__box {
  margin-top: myrem(43);

  @include mq('md') {
    margin-top: myrem(86);

  }

}

.p-offering-form__item {
  display: flex;
  flex-direction: column;

  @include mq('md') {
    flex-direction: row;
    align-items: center;
  }
}

.p-offering-form__item + .p-offering-form__item {
  margin-top: myrem(32);
}

.p-offering-form__head {
  display: flex;
  align-items: center;
  font-size: myrem(14);
  font-weight: 500;
  letter-spacing: 0.04em;

  @include mq('md') {
    flex: 0 0 myrem(190);
    font-size: myrem(16);
  }
}

.p-offering-form__head--margin-top {

  @include mq('md') {
    margin-top: myrem(10);
  }
}

.p-offering-form__item--top {
  align-items: flex-start;
}



.p-offering-form__required {
  display: inline-block;
  background-color: #F01515;
  color: $white;
  padding:myrem(1) myrem(6);
  border-radius: myrem(5);
  font-size: myrem(10);
  font-family: $font-sub;
  letter-spacing: 0.04em;
  margin-left: myrem(8);

  @include mq('md') {
    padding:myrem(2) myrem(8);
    font-size: myrem(12);
    margin-left: myrem(12);
  }
}

.p-offering-form__checkbox-wrapper {
  display: flex;
  max-width: myrem(448);
  flex-wrap: wrap;
  margin-top: myrem(10);

  @include mq('md') {
    margin-top: initial;
    margin-left: myrem(32);
  }
}

.p-offering-form__checkbox {
  display: flex;
  align-items: center;
  flex: 0 0 myrem(50);
}

.p-offering-form__checkbox {
  margin-right: myrem(12);
  margin-bottom: myrem(12);

  @include mq('md') {
    margin-right: myrem(6);
    margin-bottom: myrem(10);
  }
}

.p-offering-form__checkbox label {
  font-size: myrem(16);
  font-weight: 500;
  letter-spacing: 0.04em;
  margin-left: myrem(4);

}

input[type="text"],
input[type="number"],
input[type="tel"],
input[type="email"],
input[type="submit"]{
  appearance: none;
}

input[type="text"],
input[type="number"],
input[type="email"],
input[type="tel"] {
  margin-top: myrem(10);
  height: myrem(40);
  border-radius: myrem(3);
  padding: myrem(5);
  border: 1px solid #ccc;

  @include mq('md') {
    margin-top: initial;
    margin-left: myrem(32);
    flex: 0 0 myrem(448);
  }
}


input[type="checkbox"] {
  width: myrem(16);
  height: myrem(16);
  border-radius: myrem(2);
  border: 1px solid #ccc;
}

select {
  height: myrem(40);
  border-radius: myrem(3);
  padding: myrem(6) myrem(10);
  border: 1px solid #ccc;

  @include mq('md') {
    flex: 0 0 myrem(296);
    margin-left: myrem(32);
    padding: myrem(10) myrem(16);
  }
}

select option {
  font-size: myrem(14);

  @include mq('md') {
    font-size: myrem(16);
  }
}

textarea {
  margin-top: myrem(10);
  width: 100%;
  appearance: none;
  height: myrem(100);
  border-radius: myrem(3);
  padding: myrem(5);
  border: 1px solid #ccc;

  @include mq('md') {
    margin-top: initial;
    margin-left: myrem(32);
    flex: 0 0 myrem(448);
    height: myrem(136);
  }
}

.p-offering-form__btn {
  text-align: center;
  position: relative;
  margin-top: myrem(40);
}

.p-offering-form__submit {
  min-width: myrem(180);
  display: inline-block;
  background-color: $key;
  text-align: center;
  transition: .5s;
  color: $white;
  font-size: myrem(14);
  font-weight: 600;
  letter-spacing: 0.04em;
  padding: myrem(20) myrem(5);
  border: none;

  @include mq('md') {
    min-width: myrem(234);
    font-size: myrem(16);
  }
}

.p-offering-form__submit:hover {

  @include mq('md') {
    cursor: pointer;
    background-color: $black;
  }
}


.p-offering-form__btn-arrow {
  display: inline-block;
  position: relative;
}

.p-offering-form__btn-arrow::before {
  position: absolute;
  content: "";
  background-color: $white;
  width: myrem(14);
  height: 1px;
  top: 50%;
  transform: translateY(-50%);
  right: myrem(32);
  transition: .5s;
  z-index: 5;

  @include mq('md') {
    width: myrem(16);
  }
}

.p-offering-form__btn-arrow::after {
  position: absolute;
  content: "";
  border-top: 1px solid $white;
  border-right: 1px solid $white;
  width: myrem(5);
  height: myrem(5);
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  right: myrem(32);
  transition: .5s;

  @include mq('md') {
    width: myrem(6);
    height: myrem(6);
  }
}

.p-offering-form__btn-arrow:hover::before {

  @include mq('md') {
    right: myrem(20);
  }
}

.p-offering-form__btn-arrow:hover::after {

  @include mq('md') {
    right: myrem(20);
  }
}








