@use 'global' as *;

.p-consultation-message {
  overflow: hidden;
  position: relative;
}

.p-consultation-message::before {
  position: absolute;
  content: "";
  bottom: 0;
  right: 0;
  width: myrem(550);
  height: myrem(370);
  background: url(../images/consultation-bg1.png) no-repeat center bottom / cover;
  z-index: -1;

  @include mq('md') {
    width: myrem(1183);
    height: myrem(758);
    background: url(../images/consultation-bg1.png) no-repeat center / cover;
  }
}

.p-consultation-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  

  @include mq('md') {
    flex-direction: row;
    padding-left: myrem(175);
    margin-right: calc(50% - 50vw);
  }
}

.p-consultation-message__left {
  display: flex;
  align-items: center;
  flex-direction: column;

  @include mq('md') {
    flex-direction: row;
  }

}

.p-consultation-message__lead {
  font-size: myrem(20);
  font-weight: 500;
  line-height: 1.8;
  letter-spacing: 0.2em;
  writing-mode: vertical-rl;
  white-space: nowrap;

  @include mq('md') {
    font-size: myrem(32);
    margin-top: myrem(10);
    letter-spacing: 0.32em;

  }
}

.p-consultation-message__text {
  font-family: $font-sub;
  font-weight: 500;
  font-size: myrem(14);
  line-height: 2;
  margin-top: myrem(20);

  @include mq('md') {
    margin-top: initial;
    margin-left: myrem(160);
    width: myrem(425);
    margin-bottom: myrem(70);
    font-size: myrem(16);
  }
}

.p-consultation-message__right {

  @include mq('md') {
    margin-left: myrem(185);
  }

}

.p-consultation-message__img {
  margin-top: myrem(20);
  margin-bottom: myrem(40);

  @include mq('md') {
    margin-top: initial;
  }
}

.p-consultation-message__img img {
  aspect-ratio: 685 / 457;
  object-fit: cover;
}