@use 'global' as *;

.p-urgency {
  background-color: $bg-main;
  text-align: center;
}

.p-urgency__lead {
  font-size: myrem(18);
  font-weight: 500;

  @include mq('md') {
    font-size: myrem(32);
  }
}

.p-urgency__tel {
  margin-top: myrem(20);
  font-weight: 500;
  font-size: myrem(40);

  @include mq('md') {
    font-size: myrem(68);
  }
}

.p-urgency__tel a {
  pointer-events: auto;

  @include mq('md') {
    pointer-events: none;
  }
}

.p-urgency__tel-text {
  margin-top: myrem(24);
  font-weight: 500;
  font-size: myrem(20);

  @include mq('md') {
    margin-top: inherit;
    font-size: myrem(24);
  }
}

.p-urgency__btn {
  margin-top: myrem(20);

  @include mq('md') {
    margin-top: myrem(40);
  }
}