@use 'global' as *;

.p-concept {
  position: relative;
  overflow: hidden;
}

.p-concept__bg {
  position: relative;
}

.p-concept__bg::before {
  z-index: -1;
  position: absolute;
  content: "";
  left: myrem(-100);
  bottom: myrem(-180);
  background: url(../images/concept-bg.png) no-repeat center center/contain;
  width: myrem(600);
  height: myrem(450);

  @include mq('md') {
    left: myrem(216);
    bottom: myrem(-376);
    width: myrem(1272);
    height: myrem(900);
  }
}

.p-concept__wrapper {
  margin-top: myrem(18);
  display: flex;
  flex-direction: column;

  @include mq('md') {
    flex-direction: row;
    margin-top: initial;
    padding-left: myrem(175);
  }
}
.p-concept__body {
  display: contents;
  
  @include mq('md') {
    max-width: myrem(656);
    display: block;
  }
}

.p-concept__title {
  order: 1;
}

.p-concept__text {
  margin-top: myrem(43);
  font-size: myrem(14);
  font-weight: 500;
  line-height: 2;
  order: 3;

  @include mq('md') {
    margin-top: myrem(79);
    font-size: myrem(16);
  }

}

.p-concept__btn {
  margin-top: myrem(30);
  order: 4;

  @include mq('md') {
    margin-top: myrem(56);
  }
}

.p-concept__img {
  margin-top: myrem(40);
  margin-inline: auto;
  width: 100%;
  order: 2;
  
  @include mq('md') {
    margin-top: inherit;
    margin-left: myrem(80);
    max-width: myrem(657);
  }
}

.p-concept__img img {
  aspect-ratio: 657 / 445;
  object-fit: cover;
}