@use 'global' as *;

.c-section-title2 {
  display: inline-block;
  font-weight: 500;
  font-size: myrem(24);
  letter-spacing: 0.2em;
  writing-mode: vertical-rl;
  position: relative;

  @include mq('md') {
    font-size: myrem(32);
  }
}

.c-section-title2::before {
  position: absolute;
  content: attr(data-en);
  color: $text-sub;
  font-family: $font-en;
  font-weight: 400;
  font-size: myrem(14);
  letter-spacing: 0.08em;
  top: myrem(4);
  right: myrem(-18);

  @include mq('md') {
    font-size: myrem(17);
    top: myrem(4);
    right: myrem(-27);
  }
}